/*/Platforms */

function loadLogin(userInfo: any) {
    return async (dispatch: any) => {
        dispatch({ type: LOAD_LOGIN_REQUEST });
        try {
            const result = await axios.post(`/Tokens`, userInfo);
            dispatch({
                type: LOAD_LOGIN_SUCCESS,
                payload: {
                    user: result.data,
                    status: result.status
                },
            })


            const platformid = parseJwt(result.data.token)
            setCookie('id', userInfo.userId, 3)
            setCookie('role', userInfo.role, 3)
            setCookie('token', result.data.token, 3)
            setCookie('platformid', platformid.unique_name, 3)
            dispatch(push('/learning-portal/admin/main'))
        } catch (err) {
            dispatch({
                type: LOAD_LOGIN_FAILURE,
                payload: {
                    status: err
                },
            });
            if (err.response.status === 404) {
                dispatch({
                    type: LOAD_MESSAGE,
                    payload: {
                        message: "ไม่พบรหัสผู้ใข้",
                        severity: "error"
                    }
                })
            }
            if (err.response.status === 401) {
                dispatch({
                    type: LOAD_MESSAGE,
                    payload: {
                        message: "รหัสผ่านผิด",
                        severity: "error"
                    }
                })
            }
            else {
                dispatch({
                    type: LOAD_MESSAGE,
                    payload: {
                        message: `เกิดข้อผิดพลาด ${err.response.status}`,
                        severity: "error"
                    }
                })
            }


        }
    };
}
function loadPassword(passwordInfo: any) {
    return async (dispatch: any) => {
        dispatch({ type: LOAD_PASSWORD_REQUEST });
        try {
            const token = getCookie('token')
            const platformid = getCookie('platformid')
            const headers = {
                Authorization: `Bearer ${token}`,
            }
            const result = await axios.put(`/Platforms/${platformid}/Password`, passwordInfo, { headers });
            dispatch({
                type: LOAD_PASSWORD_SUCCESS,
                payload: {
                    passwordMessage: "เปลี่ยนรหัสผ่านสำเร็จ",
                    passwordStatus: result.status
                }
            })
            dispatch({
                type: LOAD_MESSAGE,
                payload: {
                    message: "เปลี่ยนรหัสผ่านสำเร็จ",
                    severity: "success"
                }
            })
        } catch (err) {
            dispatch({
                type: LOAD_PASSWORD_FAILURE,
                payload: {
                    passwordStatus: err
                },
            });
            if (err.response.status === 403) {
                dispatch({
                    type: LOAD_MESSAGE,
                    payload: {
                        message: `รหัสผ่านเดิมไม่ถูกต้อง`,
                        severity: "error"
                    }
                })
            }
            else {
                dispatch({
                    type: LOAD_MESSAGE,
                    payload: {
                        message: `เกิดข้อผิดพลาก ${err.response.status}`,
                        severity: "error"
                    }
                })

            }


        }
    };
}
